		#myTab {
		  list-style: none;
		  display: inline-block;
		}
		#myTab .icon {
		  font-size: 14px;
		}
		#myTab li {
		  float: left;
		}
		#myTab li.active a{
		  color: #FFF;
		  background: #ae5a21;
		}
		#myTab li a {
		  color: #FFF;
		  display: block;
		  background: #e58c61;
		  text-decoration: none;
		  position: relative;
		  height: 36px;
		  line-height: 36px;
		  padding: 0 10px 0 5px;
		  text-align: center;
		  margin-right: 23px;
		  font-size: 13px;
		  right: -12px;
		}
		
		#myTab li:nth-child(even) a {
		  background-color: #e58c61;
		}
		#myTab li:nth-child(even) a:before {
		  border-color: #e58c61;
		  border-left-color: transparent;
		}
		#myTab li:nth-child(even) a:after {
		  border-left-color: #e58c61;
		}
		#myTab li:first-child a {
		  padding-left: 15px;
		  -moz-border-radius: 4px 0 0 4px;
		  -webkit-border-radius: 4px;
		  border-radius: 4px 0 0 4px;
		}
		#myTab li:first-child a:before {
		  border: none;
		}
		#myTab li:last-child a {
		  padding-right: 15px;
		  -moz-border-radius: 0 4px 4px 0;
		  -webkit-border-radius: 0;
		  border-radius: 0 4px 4px 0;
		}
		#myTab li:last-child a:after {
		  border: none;
		}
		#myTab li a:before, #myTab li a:after {
		  content: "";
		  position: absolute;
		  top: 0;
		  border: 0 solid #e58c61;
		  border-width: 18px 10px;
		  width: 0;
		  height: 0;
		}
		#myTab li a:before {
		  left: -20px;
		  border-left-color: transparent;
		}
		#myTab li a:after {
		  left: 100%;
		  border-color: transparent;
		  border-left-color: #e58c61;
		}
		#myTab li.active a:after {
		  left: 100%;
		  border-color: transparent;
		  border-left-color: #ae5a21;
		}
		#myTab li a:hover{
		  background-color: #ae5a21;
		}
		/*#myTab li a:hover, #myTab li a:focus, #myTab li a:active {
		  background-color: #ae5a21;
		}*/
		#myTab li a:hover:before {
		  border-color: #ae5a21;
		  border-left-color: transparent;
		}
		#myTab li a:hover:after {
		  border-left-color: #ae5a21;
		}
		#myTab li a:active {
		  background-color: #ae5a21;
		}
		#myTab li a:active:before {
		  border-color: #ae5a21;
		  border-left-color: transparent;
		}
		#myTab li a:active:after {
		  border-left-color: #ae5a21;
		}
		
