input:focus { outline: none; }
/* Label */
label {
	color: #999; 
	font-size: 13px;
	font-weight: normal;
	position: absolute;
	pointer-events: none;
	left: 15px;
	top: 7px;
	transition: all 0.2s ease;
	font-weight:500;
}
input:focus ~ label, input.used ~ label {
	top: 2px !important;
	bottom:auto !important;
  transform: scale(.75); left: 1px;
  left: -9px;
	color: #bababa !important;
}
